import React from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import { movies as tileData } from "./data";
import { Formats } from "../../utilities/date";

const useStyles = makeStyles((theme) => ({
  gridList: {
    height: "92vh",
    flexWrap: "nowrap",
    transform: "translateZ(0)"
  },
  tile: {
    width: "30vw !important",
    [theme.breakpoints.down("sm")]: {
      width: "59vw !important"
    }
  },
  title: {
    color: theme.palette.warning.light,
    fontSize: "1.7em",
    whiteSpace: "initial",
    fontFamily: "Impact"
  },
  titleBar: {
    background:
      "linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)"
  },
  poster: {
    backgroundSize: "contain",
    maxHeight: "100%"
  }
}));

export default function Home() {
  const classes = useStyles();

  return (
    <GridList cellHeight="auto" className={classes.gridList} cols={2.5}>
      {tileData.map((tile) => (
        <GridListTile
          component={(props) => (
            <Link to={`/event/${Formats.toSimple(tile.date)}`} {...props} />
          )}
          className={classes.tile}
          key={tile.img}
        >
          <img src={tile.img} alt={tile.title} className={classes.poster} />
          <GridListTileBar
            titlePosition="top"
            title={Formats.legible(tile.date)}
            classes={{
              root: classes.titleBar,
              title: classes.title
            }}
          />
        </GridListTile>
      ))}
    </GridList>
  );
}
