import React from "react";
import { useParams, Redirect } from "react-router-dom";
import { moviesByDate } from "../home/data";
import { Formats } from "../../utilities/date";
import { Typography, Grid, makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "@global": {
    "body, html": {
      padding: "1em",
      overflow: "scroll !important"
    }
  },
  container: {
    maxHeight: "100vh"
  },
  message: {
    fontSize: "1.8em",
    "&:first-child:first-letter": {
      color: "#903",
      float: "left",
      "font-family": "Georgia",
      "font-size": "75px",
      "line-height": "60px",
      "padding-top": "4px",
      "padding-right": "8px",
      "padding-left": "3px"
    }
  },
  trailer: {
    "&": {
      position: "relative",
      paddingBottom: "56.25%",
      /* 16:9 */ paddingTop: "25px"
    },
    "& iframe": {
      position: "absolute",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%"
    }
  }
}));
const YoutubeEmbed = ({ movie }) => (
  <iframe
    width="100%"
    height="auto"
    src={movie.trailer}
    title={movie.title}
    frameborder="0"
    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
    allowfullscreen
  ></iframe>
);

export default function Event() {
  const classes = useStyles();
  const { date } = useParams();
  const key = Formats.fromSimple(date);
  const movie = moviesByDate[key];
  if (!movie) return <Redirect to="/oops" />;
  return (
    <Grid container direction="row" className={classes.container}>
      <Grid item xs={12}>
        <Typography className={classes.message}>{movie.message}</Typography>
      </Grid>
      <Grid item xs={12} className={classes.trailer}>
        <YoutubeEmbed movie={movie} />
      </Grid>
    </Grid>
  );
}
