import { format, parse } from "date-fns";

export function legible(dateString) {
  return format(new Date(dateString), "eeee, MMMM do, yyyy");
}

export function fromSimple(dateString) {
  return parse(dateString, "MM-dd-yyyy", new Date()).toISOString();
}

export function toSimple(dateString) {
  return format(new Date(dateString), "MM-dd-yyyy");
}
